<template>
    <div class="maxbox">
      <div class="box1">
        <h2>请输入文章</h2>
        <textarea type="text" placeholder="请输入文本内容" class="user-input" v-model="userInput"/>
    
        <div class="border"></div>
    
        <h2>请选择以下符号分隔</h2>
        <div class="box1-xz">
          <div class="fuhao">
            <a href="javascript:;" :class="{noactive:f.isCheked == 0, active:f.isCheked == 1}" v-for="(f,index) in fuhaobtn" :key="index" @click="fuhao(f,fuhaobtn)">{{ f.neirong }}</a>
          </div> 
          <button class="btn" @click="tiquText">提取文本</button>
        </div>
      </div>
    
      <div class="bottom" v-show="bottomShow">
        <div class="copy-box">
          <!-- <button class="btn-copy" @click="copyBtn"> <i class="iconfont icon-wenjianfuzhi2"></i> 复制文本</button> -->
        </div>
        <div v-for="(t,index) in this.chuliText" :key="index">{{ t }}</div>
      </div>
      <TiShi class="tishizu" v-show="tishi" :tishiText="tishiTxt"/>
    </div>
    </template>
    
    <script>
    import TiShi from  '@/components/TiShi.vue'
    export default {
      name:'TextZu',
      components:{TiShi},
        data(){
            return{
                userInput:'',
                chuliText:[],
                fuhaobtn:[
                  {
                    id:0,
                    isCheked:1,
                    neirong:'","逗号'
                  },
                  {
                    id:1,
                    isCheked:0,
                    neirong:'"。"句号'
                  },
                  {
                    id:2,
                    isCheked:0,
                    neirong:'逗号和句号'
                  }
                ],
                tishi:false,
                tishiTxt:{},
                bottomShow:false
            }
        },
        methods:{
          //用户选择的按钮
          fuhao(item,arr){
            arr.forEach(element => {
              element.isCheked = 0;
            });
            //用户点击的是哪一个
            if(item.isCheked == 1){
              item.isCheked = 0;
            }else{
              item.isCheked = 1;
            }
          },

          //点击提取按钮
          tiquText(){
            //判断用户输入是否为空
            if(this.userInput !== ''){
                this.bottomShow = true;
                this.fuhaobtn.forEach(element => {
                if(element.isCheked == 1){

                  switch(element.id){
                      case 0:
                      this.chuliText = this.userInput.split(/，|,/gi);
                      break;
                      
                      case 1:
                      this.chuliText = this.userInput.split(/。/gi);
                      break;

                      case 2:
                      this.chuliText = this.userInput.split(/，|,|。/gi);
                      break;

                      default:
                      break;
                  }
                }
              });
            }else{
              this.bottomShow = false;
              //传给提示组件的文本内容
              this.tishiTxt = {text:'⚠️ 请输入文章内容',color:'hongse'}
              //显示提示组件
              this.tishi = true;
              let timer;
              clearTimeout(timer);
              timer = setTimeout(() => {
                this.tishi = false;
              }, 1200);
            }
          },

          //复制文本按钮
          // copyBtn(){
          //   //传给提示组件的文本内容
          //   this.tishiTxt = {text:'✅ 复制成功',color:'lvse'}
          //     //显示提示组件
          //     this.tishi = true;
          //     let timer;
          //     clearTimeout(timer);
          //     timer = setTimeout(() => {
          //       this.tishi = false;
          //     }, 1200);
          // }

        },
        watch:{
          //监视输入框是否为空，空了就隐藏下方内容
          userInput(newValue,oldValue){
            if(newValue == ''){
              this.chuliText = [];
              this.bottomShow = false;
            }
          }
        }
    }
    </script>
    
